import React from "react";
import "./footer.css";

const Footer = ({ hideNextUpload, setHideNextUpload }) => {
  return (
    <div className="page-item footer">
      {hideNextUpload && (
        <button
          id="startAnalysis"
          className="start-analysis-btn"
          onClick={() => setHideNextUpload(false)}
        >
          Start Analysis
          <span className="right-arrow">&rarr;</span>
        </button>
      )}
      {!hideNextUpload && (
        <button
          id="nextUpload"
          className="next-upload-btn"
          onClick={() => setHideNextUpload(true)}
        >
          Next Upload
          <span className="right-arrow">&rarr;</span>
        </button>
      )}
    </div>
  );
};

export default Footer;
