import React, { useState } from "react";
import "./App.css";
import Upload from "./components/upload/Upload";
import NextUpload from "./components/next-upload/NextUpload";
import Footer from "./components/footer/Footer";

const App = () => {
  const [hideNextUpload, setHideNextUpload] = useState(true);
  return (
    <div className="page-container">
      <Upload />
      {!hideNextUpload && <NextUpload />}
      <Footer
        hideNextUpload={hideNextUpload}
        setHideNextUpload={setHideNextUpload}
      />
    </div>
  );
};

export default App;
